export default {
  name: 'coupons',
  components: {
  },
  data () {
    return {
      list: []
    }
  },
  created(){
    this.getData()
  },
  methods: {
    addCoupons () {
      this.$router.push({
        name: 'addCoupons'
      })
    },
    getBack () {
      this.$router.go(-1)
    },
    getData(){
      let params = {
        page: 1,
        pageSize: 100,
        merchantId: this.$store.state.merchantId
      }
      this.$http.fetchGet('/merchant/post/get/couponlist', params).then((res) => {
        this.list = res.data.data.coupons
      })
    }
  }
}



